<template>
    <div ref="echartBar" class="echart-bar" :id="id"></div>
</template>
<script>
// ECharts 
import echarts from "echarts/lib/echarts";
// 
import "echarts/lib/chart/bar";
// 
import "echarts/lib/component/tooltip";
export default {
  props: {
    selfRates: {
      type: Array,
      default: () => []
    },
    supRates: {
      type: Array,
      default: () => []
    },
    width: {
      type: Number,
      default: 0
    },
    id: {
      type: String,
      default: "echart-bar"
    }
  },
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    option: function() {
      return {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
            crossStyle: {
              color: "#999"
            },
            shadowStyle: {
              color: "transparent"
            }
          },
          // formatter: function(datas) {
          //   return datas[0].value + "/" + datas[1].value;
          // },
          formatter: "{c0}/{c1}"
        },
        color: ["#3BDABC", "#72b7f5"],
        xAxis: [
          {
            type: "category",
            data: ["", "", "", ""],
            // x
            axisLine: { show: false },
            // x
            axisTick: {
              show: false
            },
            // x
            axisLabel: {
              textStyle: {
                color: "#999"
              }
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            min: 0,
            max: 5,
            interval: 1,
            show: true,
            // 
            splitLine: {
              show: true,
              //  
              lineStyle: {
                // 
                color: ["#eee"]
              }
            },
            axisLabel: {
              formatter: "{value}",
              textStyle: {
                color: "#c7c7c7"
              }
            },
            // y
            axisLine: { show: false },
            // y
            axisTick: {
              show: false
            }
          }
        ],
        series: [
          {
            name: "",
            type: "bar",
            center: ["50%", "60%"],
            data: this.selfRates,
            barWidth: 12,
            itemStyle: {
              normal: {
                barBorderRadius: [6, 6, 0, 0],
                color: new echarts.graphic.LinearGradient(1, 0, 0, 1, [
                  { offset: 0, color: "#3BDABC" },
                  { offset: 1, color: "#3BDABC" }
                ])
              }
            }
          },
          {
            name: "",
            type: "bar",
            data: this.supRates,
            barWidth: 12,
            itemStyle: {
              normal: {
                // 
                barBorderRadius: [6, 6, 0, 0],
                color: new echarts.graphic.LinearGradient(1, 0, 0, 1, [
                  { offset: 0, color: "#72b7f5" },
                  { offset: 1, color: "blue" }
                ])
              }
            }
          }
        ]
      };
    }
  },
  mounted() {
    this.myChart = echarts.init(this.$refs.echartBar);
    this.myChart.setOption(this.option);
  },
  watch: {
    // 
    width: function() {
      const width = document.getElementById(this.id).clientWidth;
      this.myChart.resize({ width });
    },
    selfRates: function() {
      this.myChart.setOption(this.option);
    },
    supRates: function() {
      this.myChart.setOption(this.option);
    }
  }
};
</script>

<style scoped>
.echart-bar {
  height: 100%;
  width: 100%;
}
</style>



